input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
	color: $color__gray;
	border-bottom: 1px solid $color__lightgray;
	padding: 15px 5px;
	background-color: #fff;
	&:focus {
		color: $color__green;
	}
}

select {
	border-bottom: 1px solid $color__lightgray;
}

textarea {
	width: 100%;
}
